body{
	min-height: 500px;
	margin: 0;
	padding:0;
	background-image:linear-gradient(to right bottom,#2E2EFE,#2E9AFE);
	background-repeat: no-repeat;
	color: #fff;
	text-align: center;
	font-size: 20px;
}
a{
	color: #fff;
}
.header{
	position: relative;
	height: 20%;
}
.title{
	text-decoration: none;
	
}
.h-right{
	position: absolute;
	right: 1em;
	top:0;
	height: 100%;
}
.h-right a{
	display: block; 
	font-size: 0.7em;
	opacity: 0.8;
}
/* == form ==*/
.form{
	margin-top:2em;
	width: 300px;
	display: inline-block;
	position: relative;
}
.form label{
	font-size: 10px;
	float: left;
	margin-top:2em;
	margin-bottom: 1px;
	opacity: 0.7;
}
.form input{
	float: left;
	padding: 0;
	width: 100%;
}
input[type="text"],input[type="password"]{
	border: none;
	border-bottom: 1px solid #fff;
	background: transparent;
	outline: none;
	height: 30px;
	font-size: 15px;
	color: #fff;
}
.form .forget-password{
	margin-top: 0.5em;
	float: right;
	font-size: 10px;
	opacity: 0.7;
	text-decoration: none;
}
form .sign-facebook{
	clear: right;
	display: inline-block;
	box-sizing: border-box;
	width: 100%;
	background-color: #fff;
	margin-top: 2em;
	height: auto;
	border-radius: 0.5em;
	height: 40px;
}
.sign-facebook a{
	text-decoration: none;
	color: blue;
	line-height:0px;
}
input[type="submit"]{
	display: block;
	margin-top: 40px;
	background-color: #2E9AFE;
	border: none;
	border-radius: 0.5em;
	height: 40px;
}